export default {
	data() {
		return {
			keywords: '',
			trailerList: ["../../static/poster/civilwar.jpg",
				"../../static/poster/civilwar.jpg",
				"../../static/poster/civilwar.jpg",
				"../../static/poster/civilwar.jpg",
				"../../static/poster/civilwar.jpg",
				"../../static/poster/civilwar.jpg",
				"../../static/poster/civilwar.jpg",
				"../../static/poster/civilwar.jpg",
				"../../static/poster/civilwar.jpg",
				"../../static/poster/civilwar.jpg",
				"../../static/poster/civilwar.jpg",
				"../../static/poster/civilwar.jpg",
				"../../static/poster/civilwar.jpg",
				"../../static/poster/civilwar.jpg",
				"../../static/poster/civilwar.jpg",
			],
			page: 1,
			totalPages: 1,
			index: 1
		}
	},
	onReachBottom() {
		var that = this;
		let newTraulerList = ["../../static/poster/justice.png", "../../static/poster/justice.png",
			"../../static/poster/justice.png"
		]

		if (that.index <= 2) {
			that.trailerList = that.trailerList.concat(newTraulerList)
			that.index++;
		} else {
			uni.showToast({
				title: "没有数据了！",
				icon: "success"
			})
		}
	},
	methods: {
		search(e) {
			console.log(e);
			let value = e.detail.value;
			this.keywords = value;
			this.trailerList = ["../../static/poster/civilwar.jpg"];
			uni.showToast({
				title: value,
				icon: "success"
			})
			console.log(value)
		},
		movie(index) {
			uni.navigateTo({
				url:"../movie/movie?index="+index
			})
			console.log(index)
		}
	}
}
